import { h, Component } from 'preact';
import { bindActionCreators } from 'redux';
import { connect } from 'preact-redux';
import LogInForm from '../../components/log-in/login-in-form';
import { logIn } from '../../actions/user';
import Redirect from '../../components/global/redirect';
import { routes } from '../../components/app';

class LogIn extends Component {

	fetchPage= (props) => {
		window.scrollTo(0, 0);
	}

	constructor(props){
		super(props);
		this.state = {
			loggedIn: false
		};
	}
	componentDidMount(){
		this.fetchPage(this.props);
	}
	componentWillReceiveProps(nextProps) {
		if (Object.keys(this.props.userData).length === 0 && Object.keys(nextProps.userData).length !== 0) {
			this.setState({
				loggedIn: true,
				isPro: !!nextProps.userData.pro
			});
		}
	}
	render() {
		return (!this.state.loggedIn) ? (
			<div id="login" className="uk-container uk-container-small" style={{ marginTop: '125px' }}>
				<h1>Log in</h1>
				<LogInForm logIn={this.props.logIn} logInFailure={this.props.logInFailure} />
			</div>
		) : (this.state.isPro) ?
			<Redirect to={routes.MY_CLIENTS} /> : <Redirect to={routes.MY_PROS} />;
	}
}

const mapStateToProps = (state) => ({
	logInFailure: state.logInFailure,
	userData: state.loggedInUser
});

const mapDispatchToProps = dispatch => bindActionCreators({
	logIn
}, dispatch);

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(LogIn);



